import React, { Component } from "react";
import CONSTANTS from "../common/PeertalConstants";
import UserObject from "../models/UserObject";
import PostObject from "../models/PostObject";
import { connect } from "react-redux";
import "./css/PostItem.scss";

type myProps = {
  data: PostObject;
  callback?: Function;
  user?: any;
};
type myState = {};
class PostItem extends Component<myProps, myState> {
  constructor(props: myProps) {
    super(props);
  }

  render() {
    const data = this.props.data;
    const votedNo = data.voteData.total;
    let tagList = "";
    tagList = CONSTANTS.renderListPeople(data.taggedUsers.map((item: any) => item.fullName));
    let votedList = "";
    if (data.voteData != null) {
      votedList = "Voted by " + CONSTANTS.renderListPeople(data.voteData.votedUsers.map((item: any) => item.fullName));
    }
    const commentNo = data.totalComments;
    const timeAgo = CONSTANTS.getTimeDifference(data.createdAt);

    let fullName, avatarUrl;
    const user = data.user || new UserObject();
    fullName = user.fullName;
    avatarUrl = user.avatarUrl || CONSTANTS.DEFAULT_AVATAR;
    const locationAddress = data.locationAddress || "";
    // const exRate = this.props.user.settings.exchangeRate;
    // const exCurrency = this.props.user.settings.preferredCurrency || "USD";
    // let postMoney = "";
    // if (data.money.totalCoin > 0) {
    //   postMoney = "$ " + (data.money.totalCoin * exRate).toFixed(2) + " " + exCurrency;
    // }
    let groupAvatar = null;
    if (data.voteData.votedUsers.length != null) {
      groupAvatar = data.voteData.votedUsers.map((item: any) => item.avatarUrl);
    }
    //calculate status of like/dislike...
    const likeButtonEnabled = data.voteData.upData.active === true;
    const dislikeButtonEnabled = data.voteData.downData.active === true;
    const callback = this.props.callback;
    const shareNo = 0; //not yet have in our system
    /* Hide wallet and money transfer feature for ios */
    /* let options = ['Report', 'Reward', 'Cancel']; */
    let options = ["Report", "Reward", "Cancel"];

    return (
      <div className="post-item-block">
        <div className="post-item-main">
          <div className="post-item-main__left">
            <img src={avatarUrl} className="kuky-avatar--main"></img>
          </div>
          <div className="post-item-main__right">
            <div className="post-item-main__right-name">{fullName}</div>
          </div>
        </div>

        <div className="post-item-main__content">{data.content}</div>
      </div>
    );
  }
}
const MapStateToProps = (store: any) => ({ user: store.user });
const PostItemContainer = connect(MapStateToProps)(PostItem);
export default PostItemContainer;
