import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { withRouter, RouteComponentProps, Link } from "react-router-dom";
import { connect } from "react-redux";
import { library } from "@fortawesome/fontawesome-svg-core";
import { fas } from "@fortawesome/free-solid-svg-icons";
import "./css/LeftMenu.scss";

library.add(fas);

type myProps = RouteComponentProps & any;
class LeftMenu extends Component<myProps> {
  renderItems() {
    var menu: Array<any>;
    menu = this.props.user.menu;
    return menu.map((item: any, index) => (
      <Link key={index} className="menu-item" to={item.link}>
        <FontAwesomeIcon icon={{ iconName: item.icon, prefix: "fas" }} color="grey" />
        <div className="right-item">{item.name}</div>
      </Link>
    ));
  }
  render() {
    return <div className="main">{this.renderItems()}</div>;
  }
}

const MapStateToProps = (store: any) => ({ user: store.user });
const LeftMenuContainer = connect(MapStateToProps, null)(LeftMenu);
export default withRouter(LeftMenuContainer);
