import React, { Component } from "react";
import "./css/Header.scss";
import { connect } from "react-redux";
import { withRouter, RouteComponentProps, Link } from "react-router-dom";
import logo from "../../imgs/logo_sm.png";
import { BsSearch } from "react-icons/bs";
import { FaQrcode, FaRocketchat } from "react-icons/fa";
import { IoIosNotifications, IoIosArrowDown } from "react-icons/io";

type myProps = RouteComponentProps & any;
class Header extends Component<myProps> {
  render() {
    const name = this.props.user.fullName;
    const userName = this.props.user.userName;
    const avatarUrl = this.props.user.avatarUrl;
    return (
      <div className="main-header">
        <div className="main-header__left">
          <Link to="/">
            <img className="main-header__left__image" src={logo} alt="logo"></img>
          </Link>
        </div>
        <div className="main-header__main">
          <div className="main-header__main__box">
            <input className="main-header__main__input" placeholder="Search"></input>
            <BsSearch className="main-header__main__search-icon icon" />
          </div>
        </div>
        <div className="main-header__right">
          <FaQrcode className="icon" />
          <FaRocketchat className="icon" />
          <IoIosNotifications className="icon"></IoIosNotifications>
          <img src={avatarUrl} className="kuky-avatar--main icon" alt="avatar"></img>
          <div>
            <div className="main-header__right__name">{name}</div>
            <div style={{ fontSize: "0.8em" }}>{userName}</div>
          </div>
          <IoIosArrowDown className="icon" />
        </div>
      </div>
    );
  }
}

const MapStateToProps = (store: any) => ({ user: store.user });
const HeaderContainer = connect(MapStateToProps, null)(Header);
export default withRouter(HeaderContainer);
