import React, { Component, MouseEvent } from "react";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import "./css/WelcomePage.scss";
import LeftMenu from "../components/LeftMenu";
type myState = {};
type myProps = {};
export default class WelcomePage extends Component<myProps, myState> {
  constructor(props: myProps) {
    super(props);
    this._onClickButtonGG = this._onClickButtonGG.bind(this);
  }
  _onClickButtonGG(event: MouseEvent) {
    alert("hello click");
    window.location.href = "/home";
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-2 kuky-col-left">
            <LeftMenu />
          </div>
          <div className="col-10 kuky-col-right">
            <div className="kuky-main-left"></div>
            <div className="kuky-main-center main">
              <div className="welcome-header">Please sign in</div>
              <div className="page-header2">
                <Link to="/discover">Discover here</Link>
              </div>
              <div>go to login</div>
              <div className="main-login-block">
                <Link to="login" className="email-btn">
                  By Email
                </Link>
                <Link to="login" className="email-btn">
                  By SMS
                </Link>
              </div>
              <div> Or you can login by</div>
              <div className="social-login-block">
                <Button className="social-login-block__btn">Facebook</Button>
                <Button className="social-login-block__btn" onClick={this._onClickButtonGG}>
                  Google
                </Button>
              </div>
            </div>
            <div className="kuky-main-right"></div>
          </div>
        </div>
      </div>
    );
  }
}
