import React, { Component, MouseEvent, UIEvent, ChangeEvent } from "react";
import "./css/LoginCodePage.scss";
import { connect } from "react-redux";
import { Button } from "react-bootstrap";
import { Redirect, useHistory, useParams, withRouter, RouteComponentProps } from "react-router-dom";
import { getPosts } from "../actions/postActions";
import PostItem from "../components/PostItem";
import { loginCodeInput } from "../actions/userActions";
type myProps = RouteComponentProps & any;
type myState = { txtUsername: string; txtPassword: string; isLoading: Boolean; userName: string; txtCode: string };
class LoginCodePage extends Component<myProps, myState> {
  constructor(props: any) {
    super(props);
    this.state = { txtUsername: "", txtPassword: "", isLoading: false, userName: "xxx", txtCode: "" };
    this._handleSubmit = this._handleSubmit.bind(this);
    this._handleCodeChange = this._handleCodeChange.bind(this);
    this._handlePasswordChange = this._handlePasswordChange.bind(this);
    this.renderEmail = this.renderEmail.bind(this);
  }
  _handleSubmit(e: MouseEvent) {
    const his = this.props.history;
    loginCodeInput(
      this.state.userName,
      this.state.txtCode,
      (res: any) => {
        console.log("res", res);
        const resData = res.data;
        const data = resData.data;
        if (res.status === 200) {
          this.props.dispatch({
            type: "USER_RECEIVE_LOG_IN_SUCCESSFULLY",
            data: {
              user_data: data.user_data,
              accessToken: data.user_data.token,
              settings: data.settings
            }
          });
          his.push("/discover");
        }
      },
      (e: any) => {
        console.log("e", e);
      }
    );
  }

  _handleCodeChange(e: React.ChangeEvent<HTMLInputElement>) {
    this.setState({ txtCode: e.target.value });
  }
  _handlePasswordChange(newText: React.ChangeEvent<HTMLInputElement>) {
    this.setState({ txtPassword: newText.target.value });
    console.log("text change", newText);
  }
  componentDidMount() {
    console.log("match", this.props.match);
    let params: any;
    params = this.props.match.params;
    this.setState({ userName: params.userName });
    console.log("location", this.props.location);
  }
  renderEmail() {
    return <div>{this.state.userName}</div>;
  }

  render() {
    return (
      <div className="container">
        <div className="row">
          <div className="col">
            <div className="kuky-header-1__light top-header">Activation Code</div>
            <div className="kuky-header-1__bold">was sent to your email</div>
            <div>Enter your code below</div>
            <div className="login-block main-block">
              <div>
                <div>Security Code</div>
                <input
                  className="kuky-form-input"
                  type="text"
                  value={this.state.txtCode}
                  onChange={(e: React.ChangeEvent<HTMLInputElement>) => this._handleCodeChange(e)}
                  placeholder="enter your code"
                />
              </div>
              <Button className="button-top" onClick={(e: MouseEvent) => this._handleSubmit(e)}>
                verify
              </Button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const MapStateToProps = (store: any) => ({ user: store.user });
// const MapDispatchToProps = (dispatch: any) => {
//   dispatch;
// };
const LoginCodeContainer = connect(MapStateToProps, null)(LoginCodePage);
export default withRouter(LoginCodeContainer);
