import React, { Component, MouseEvent, UIEvent } from "react";
import "./css/DiscoverPage.scss";
import { withRouter, RouteComponentProps } from "react-router-dom";
import { connect } from "react-redux";
import { getPosts } from "../actions/postActions";
import PostItem from "../components/PostItem";
import LeftMenu from "../components/LeftMenu";
import debounce from "lodash.debounce";

type myProps = RouteComponentProps & any;
type myState = { isSearch: boolean; posts: Array<any>; isLoading: boolean };
class DiscoverPage extends Component<myProps, myState> {
  constructor(props: any) {
    super(props);
    this.state = { posts: [], isSearch: false, isLoading: false };
    this._renderPosts = this._renderPosts.bind(this);
    this._onScroll = this._onScroll.bind(this);
    // Binds our scroll event handler
    window.onscroll = debounce(() => {
      if (this.state.isLoading) return;
      if (window.innerHeight + document.documentElement.scrollTop === document.documentElement.offsetHeight) {
        this._loadData(this.state.posts.length);
      }
    }, 100);
  }
  componentDidMount() {
    this._loadData();
  }
  _onScroll(e: UIEvent) {
    console.log("scroll", e);
    alert("dd");
  }
  _loadData(from = 0) {
    this.setState({ isLoading: true });
    getPosts("", 0, 20, from, undefined, 0, undefined, (response: any) => {
      this.setState({ posts: this.state.posts.concat(response.data.data.posts), isLoading: false });
      console.log(response);
    });
  }
  _renderPosts() {
    return this.state.posts.map((item: any, index: number) => {
      return <PostItem data={item} key={index} />;
    });
  }

  render() {
    const user = this.props.user;
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-2 kuky-col-left">
            <LeftMenu />
          </div>
          <div className="col-10 kuky-col-right">
            <div className="kuky-main-left"></div>
            <div className="kuky-main-center">
              {this._renderPosts()}
              {this.state.isLoading ? <div className="loading">...Loading...</div> : null}
            </div>
            <div className="kuky-main-right"></div>
          </div>
        </div>
      </div>
    );
  }
}
const MapStateToProps = (store: any) => ({ user: store.user });
const DiscoverContainer = connect(MapStateToProps, null)(DiscoverPage);
export default withRouter(DiscoverContainer);
