import React, { Component } from "react";
import LeftMenu from "../components/LeftMenu";

export default class StaticPage extends Component {
  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <div className="col-2 kuky-col-left">
            <LeftMenu />
          </div>
          <div className="col-10 kuky-col-right">
            <div className="kuky-main-left"></div>
            <div className="kuky-main-center"></div>
            <div className="kuky-main-right"></div>
          </div>
        </div>
      </div>
    );
  }
}
